import route from "@/common/public.js"
import Prompt from '@/components/zz-prompt/index.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import tarBar from "@/components/customize/tarBar.vue"
export default {
	components: {Prompt,uniIcon,tarBar},
	data() {
		return {
		  Signboard:'',
		  Signguid:'',
		  userInfo: '',
		  userName: '',
		  dis:'', //用于判断是公司账号还是子账号
		  statusMsg:'', //登录状态
		  btnExit:true,
		  btnLogin:true,
		  titleName:'',
		  userType:"", //账号类型 子账号-主账号
		}
	},
	methods:{
		userClick(evt){
			if(this.statusMsg=="未登录"){
				uni.showToast({
					title:'当前未登录，暂无权操作',
					icon:'none'
				})
			}else{
				switch (evt){
					case 1:
						uni.navigateTo({
							url:"../user/companyInfo"
						})
						break;
					case 2:
						uni.navigateTo({
							url:"../user/personalInfo"
						})
						break;
					case 3:
						uni.navigateTo({
							url:"../order/orderpageinfo"
						})
						break;
					case 4:
						uni.navigateTo({
							url:"../user/bill"
						})
						break;
					case 5:
						uni.navigateTo({
							url:"../user/recharge_center"
						})
						break;
					case 6:
						uni.navigateTo({
							url:"../user/safety"
						})
						break;
					case 7:
						uni.navigateTo({
							url:"../user/useraddress"
						})
						break;
					case 8:
						if(this.dis=="COMPANY"){
							uni.navigateTo({
								url:'../user/subaccount'
							})
						}else{
							uni.showToast({
								title:'当前账号为子账号，您暂时无权查看',
								icon:'none'
							})
						}
						break;
					default:
						break;
				}
			}
		},
		jumpLogin(){
			uni.navigateTo({
				url:"../index/login"
			})
		},
		/* 点击子账号管理触发的事件 */
		/* subaccountTap(evt){
			if(this.dis=="COMPANY"){
				uni.navigateTo({
					url:'../user/subaccount'
				})
			}else{
				uni.showToast({
					title:'当前账号为子账号，您暂时无权查看',
					icon:'none'
				})
			}
		}, */
		/* 点击退出触发的事件 */
		OutClick(evt){
			uni.request({
				url:route.variable+'/mobile/index/loginOut',
				method:'GET',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
				},
				success: (res) => {
					uni.clearStorageSync();
					uni.setStorageSync('shop',0);
					uni.redirectTo({
						url:'../index/index'
					});
				},
				fail:(res)=>{
					uni.hideLoading()
					uni.showToast({title: '退出失败'+'错误码201',icon:"none"});
				}
			})
			// 移除购物车角标
			uni.removeTabBarBadge({
				index:2
			})
		}
	},
	onShow:function()
	{
		var jsonList=uni.getStorageSync("jsonList");
		if(jsonList){
			var data = JSON.parse(jsonList); //JSON字符串转对象
			this.Signboard = data.Ident_Signboard;
			this.Signguid = data.Ident_Signguid;
			this.userName = data.userName;
			this.dis=data.dis;
			
			this.titleName='用户名：'+this.userName;
			this.statusMsg="当前登录";
			this.btnExit=false;
			this.btnLogin=true;
			 /* 判断是公司账号还是子账号 */
			if(this.dis=="COMPANY"){
				this.userType="账号类型：主账号";
			}else{
				this.userType="账号类型：子账号";
			}
		}else{
			this.userType="";
			this.titleName="";
			this.userName="";
			this.statusMsg="未登录";
			this.btnExit=true;
			this.btnLogin=false;
		}
		
		/* if(jsonList==""||jsonList==null){
			this.statusMsg="未登录";
			this.btnExit=true;
			this.btnLogin=false;
		}else{
			this.titleName='用户名：'+this.userName;
			this.statusMsg="当前登录";
			this.btnExit=false;
			this.btnLogin=true;
		} */
	}
}
